import axios from 'axios';
import { fetchAuthSession } from 'aws-amplify/auth';
import {BACKEND_HOST} from "./host";

// Tworzenie instancji axios
const http = axios.create({
    baseURL: `${BACKEND_HOST}/api`,
    withCredentials: true,
});

// Interceptor do dodawania tokenu do nagwkw
http.interceptors.request.use(
    async (config) => {
        const apiBaseUrl = `${BACKEND_HOST}/api`;

        // Jeli URL nie pasuje, kontynuuj bez modyfikowania nagwkw
        if (!config.url.startsWith(apiBaseUrl)) {
            return config;
        }

        // Pobieranie sesji uytkownika z AWS Amplify
        try {
            const session = await fetchAuthSession();
            // Jeli sesja jest prawidowa, dodaj token do nagwkw
            if (session && session.tokens && session.tokens.idToken) {
                const token = session.tokens.idToken.toString();
                config.headers['Authorization'] = `Bearer ${token}`;
            }
        } catch (error) {
            console.error('Error fetching auth session:', error);
        }

        return config;
    },
    (error) => {
        // W przypadku bdu zwr obiekt bdu
        return Promise.reject(error);
    }
);

// Exportowanie instancji API
export default http;
