import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import AuthService from '../services/AuthService';  // Zamy, e AuthService jest w tym samym folderze

const AuthGuard = ({ children, shouldBeLoggedIn = true }) => {
    const [isAuthenticated, setIsAuthenticated] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        // Sprawd, czy uytkownik jest zalogowany
        const checkAuth = async () => {
            try {
                const isLoggedIn = await AuthService.isLoggedIn();
                if (shouldBeLoggedIn && !isLoggedIn) {
                    navigate('/login');  // Przekieruj na stron logowania, jeli uytkownik nie jest zalogowany
                } else if (!shouldBeLoggedIn && isLoggedIn) {
                    navigate('/');  // Przekieruj na stron gwn, jeli uytkownik jest zalogowany i prbuje wej na stron logowania
                } else {
                    setIsAuthenticated(true);  // Uytkownik spenia warunki, moemy renderowa dzieci
                }
            } catch (error) {
                console.error('Error during authentication check:', error);
                navigate('/login');  // W razie bdu przekierowujemy na stron logowania
            }
        };

        checkAuth();
    }, [shouldBeLoggedIn, navigate]);

    if (isAuthenticated === null) {
        // Wci w trakcie sprawdzania logowania - nie renderuj niczego, a stan zostanie ustalony
        return null;
    }

    return <>{children}</>;  // Jeli uytkownik jest autoryzowany, renderuj dzieci (tras)
};

export default AuthGuard;
